import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { SwiperDatePickerService, TaiwanDatePipe, DateType } from 'merc-lib';
import * as moment from 'moment';
import { NzModalService } from 'ng-zorro-antd/modal';
import { DEVICE } from 'projects/tc-lib/src/lib/const/device.const';
import { ICON_PATH } from 'projects/tc-lib/src/lib/const/icon-path.const';
import { ITableSetting } from 'projects/tc-lib/src/lib/interfaces/table-setting';
import { PageSet } from 'projects/tc-lib/src/public-api';
import { SignUpStatus } from '../../../core/enums/signup-status.enum';
import { ClassInfoModalComponent } from '../../../shared/components/class-info-modal/class-info-modal.component';
import { SignupErrorModalComponent } from '../../../shared/components/signup-error-modal/signup-error-modal.component';
import { SignupModalComponent } from '../../../shared/components/signup-modal/signup-modal.component';
import { SharedService } from '../../../shared/shared.service';
import { REGISTER_TYPE } from '../../const/register-type.const';
import { QuizStatus, QuizAction, OutsideExamType } from '../enums/quiz.enum';
import { AdmissionNoticeModalComponent } from '../modal/admission-notice-modal/admission-notice-modal.component';
import { BillModalComponent } from '../modal/bill-modal/bill-modal.component';
import { ChangePlaceComponent } from '../modal/change-place/change-place.component';
import { outsideMockTable } from './outside-mock';

@Component({
  selector: 'app-outside-quiz',
  templateUrl: './outside-quiz.component.html',
  styleUrls: ['./outside-quiz.component.scss']
})
export class OutsideQuizComponent implements OnInit {

  get ICON_PATH(): any { return ICON_PATH; }
  /** TODO: 假資料 */
  quizList: any[] = outsideMockTable;
  searchForm!: FormGroup;
  QuizStatus = QuizStatus;
  QuizAction = QuizAction;
  /** 註冊狀態 */
  SignUpStatus = SignUpStatus;
  pageSet = new PageSet();
  DEVICE = DEVICE;
  tableSetting: ITableSetting[] = [
    { header: '測驗名稱', primaryKey: 'title' },
    { header: '考區', primaryKey: 'location' },
    { header: '期別', primaryKey: 'class' },
    { header: '測驗日期', primaryKey: 'quiz_date' },
    { header: '報名日期', primaryKey: 'register_date' },
  ];
  btnList: any[] = [
    // hidden data.register_status !== QuizStatus.paying
    { title: '印繳款單', type: QuizAction.printSlip },
    // hidden data.register_status !== QuizStatus.paid
    { title: '變更考區', type: QuizAction.changePlace },
    // hidden data.register_status !== QuizStatus.succuss
    { title: '試場資訊', type: QuizAction.info },
    // hidden data.register_status === QuizStatus.noStatus
    { title: '取消報名', type: QuizAction.cancel },
    // hidden data.register_status !== QuizStatus.noStatus
    { title: '我要報名', type: QuizAction.join },
    // hidden data.register_status === QuizStatus.fail
    { title: '報名失敗', type: QuizAction.failReason },
  ];
  locationList: { label: string, value: any }[] = [
    { label: '台北區', value: '台北區' },
    { label: '台中區', value: '台中區' },
    { label: '桃竹區', value: '桃竹區' },
    { label: '嘉義區', value: '嘉義區' },
    { label: '台南區', value: '台南區' },
  ];
  constructor(
    public sharedService: SharedService,
    private formBuilder: FormBuilder,
    private swiperDatePickerService: SwiperDatePickerService,
    private taiwanDatePipe: TaiwanDatePipe,
    private modalService: NzModalService,
  ) { }

  ngOnInit(): void {
    // TODO: 暫時這樣寫
    this.quizList = this.quizList.map((item) => {
      if (item) {
        item.btnList = this.filterBtn(item.register_status);
      }
    });
    this.handleShowData();
    this.pageSet.initialize(outsideMockTable.length);
    this.initForm();

  }
  private initForm(): void {
    this.searchForm = this.formBuilder.group({
      nature: [{ value: 'outside', disabled: true }],
      keyword: [''],
      str_date: [moment().add(-2, 'M').valueOf()],
      end_date: [moment().add(1, 'M').valueOf()],
    });
  }
  /**
   * TODO: 找更好看的寫法
   * @param status QuizStatus
   * @returns btn[]
   */
  private filterBtn(status: QuizStatus): any[] {
    const list = this.btnList;
    const result: any[] = [];
    list.forEach((item) => {
      switch (status) {
        case QuizStatus.noStatus:
          if (item.type === QuizAction.join) {
            result.push(item);
          }
          break;
        case QuizStatus.fail:
          if (item.type === QuizAction.failReason) {
            result.push(item);
          }
          break;
        case QuizStatus.paying:
          if (item.type === QuizAction.printSlip) {
            result.push(item);
          }
          break;
        case QuizStatus.paying:
          if (item.type === QuizAction.printSlip) {
            result.push(item);
          }
          break;
        case QuizStatus.paid:
          if (item.type === QuizAction.changePlace) {
            result.push(item);
          }
          break;
        case QuizStatus.succuss:
          if (item.type === QuizAction.info) {
            result.push(item);
          }
          break;
        default:
          break;
      }
      if (item.type === QuizAction.cancel && status !== QuizStatus.noStatus && status !== QuizStatus.fail) {
        result.push(item);
      }
    });
    return result;
  }

  /**
   * 按鈕分配
   */
  onPositiveClick(action: QuizAction, data: any): void {
    switch (action) {
      case QuizAction.join:
      case QuizAction.cancel:
        this.onSignClick(action, data);
        break;
      case QuizAction.info:
        this.onExamInfoClick(data);
        break;
      case QuizAction.changePlace:
        this.onChangePlaceClick(data);
        break;
      case QuizAction.printSlip:
        this.onPrintClick();
        break;
      case QuizAction.failReason:
        this.onRejectReason();
        break;
      default:
        break;
    }
  }

  onSearchClick(): void {

  }
  /**
   * 印繳款單
   */
  onPrintClick(): void {
    this.modalService.create({
      nzContent: BillModalComponent,
      nzWidth: 850,
      nzMaskClosable: false,
      nzFooter: null,
      nzBodyStyle: { padding: '12px 24px' },
      nzComponentParams: {
      }
    });

  }

  onExamInfoClick(data?: any): void {
    if (data.outside_exam_type === OutsideExamType.invest) {
      const url = 'https://edu.tii.org.tw/exam/users/user_status';
      window.open(url, '_blank');
    } else {
      this.modalService.create({
        nzContent: AdmissionNoticeModalComponent,
        nzWidth: 850,
        nzMaskClosable: false,
        nzFooter: null,
        nzCentered: true,
        nzBodyStyle: { padding: '12px 24px' },
        nzComponentParams: {
          outsideExam: data.outside_exam_type
        }
      });
    }
  }
  onChangePlaceClick(quiz: any): void {
    this.modalService.create({
      nzContent: ChangePlaceComponent,
      nzWidth: 600,
      nzMaskClosable: false,
      nzFooter: null,
      nzBodyStyle: { padding: '12px 24px' },
      nzComponentParams: {
        quiz
      }
    });
  }
  /**
   * 報名
   * @param action 
   * @param data 
   */
  onSignClick(action: QuizAction, data: any): void {
    let registerAction;
    switch (action) {
      case QuizAction.join:
        registerAction = SignUpStatus.isOpen;
        break;
      case QuizAction.cancel:
        registerAction = SignUpStatus.signed;
        break;
      default:
        break;
    }
    this.modalService.create({
      nzContent: SignupModalComponent,
      nzWidth: 600,
      nzMaskClosable: false,
      nzFooter: null,
      nzBodyStyle: { padding: '12px 24px' },
      nzComponentParams: {
        action: registerAction,
        // action: SignUpStatus.signed,
        type: REGISTER_TYPE.OUTSIDE_EXAM,
        outsideExam: data.outside_exam_type
      }
    });
  }
  /**
   * 清除鈕
   */
  onClearClick(): void {

  }
  /** 換頁 */
  onChangePageIndex(pageIndex: number): void {
    if (pageIndex < 1 || pageIndex > this.pageSet.totalPages) { return; }
    this.pageSet.currentPage = pageIndex;
    this.handleShowData();
    console.log(this.pageSet.currentPage);
  }

  /** 顯示資料 */
  handleShowData(): void {
    this.quizList = outsideMockTable.slice(
      (this.pageSet.currentPage - 1) * this.pageSet.currentPageSize,
      (this.pageSet.currentPage) * this.pageSet.currentPageSize
    );
  }
  /** 顯示詳細資料 */
  showInfo(event: any): void {
    const modal = this.modalService.create({
      nzContent: ClassInfoModalComponent,
      nzWidth: 600,
      nzClosable: true,
      nzFooter: null,
      nzClassName: 'white-close-x',
      nzBodyStyle: {
        padding: '0',
      },
      nzComponentParams: {
        event
      }
    });
  }
  onRejectReason(): void {
    this.modalService.create({
      nzContent: SignupErrorModalComponent,
      nzWidth: 400,
      nzMaskClosable: false,
      nzFooter: null,
      nzBodyStyle: { padding: '12px 24px' },
    });
  }
}
