import { Pipe, PipeTransform } from '@angular/core';
import { OutsideExamType } from '../enums/quiz.enum';

@Pipe({
  name: 'noticeType'
})
export class NoticeTypePipe implements PipeTransform {

  transform(type: unknown): unknown {
    switch (type) {
      case OutsideExamType.foreignCurrency:
        return '銷售外幣收付非投資型保險商品資格測驗';
      case OutsideExamType.invest:
        return '投資型考試';
      case OutsideExamType.professional:
        return '資格測驗-專業科目';
      case OutsideExamType.common:
        return '資格測驗-共同科目';
      default:
        return '-';
    }
  }

}
