import { Component, Input, OnInit } from '@angular/core';
import { ICON_PATH } from 'projects/tc-lib/src/lib/const/icon-path.const';
import { CertificateInvestType, OutsideExamType } from '../../enums/quiz.enum';

@Component({
  selector: 'app-reissue-certificate-modal',
  templateUrl: './reissue-certificate-modal.component.html',
  styleUrls: ['./reissue-certificate-modal.component.scss']
})
export class ReissueCertificateModalComponent implements OnInit {
  /** 外部測驗類型 */
  @Input() outsideExam!: OutsideExamType;
  ICON_PATH = ICON_PATH;
  OutsideExamType = OutsideExamType;
  CertificateInvestType = CertificateInvestType;
  constructor() { }

  ngOnInit(): void {
  }
  /**
   * 下載連結
   */
  onDownloadClick(type?: CertificateInvestType): void {
    let url = '';
    switch (this.outsideExam) {
      case OutsideExamType.professional:
      case OutsideExamType.common:
      case OutsideExamType.foreignCurrency:
        url = 'https://www.mli.com.tw/aes/winz/p-trainning-10.pdf';
        break;
      case OutsideExamType.invest:
        // 遺失及變更資料：連結https://www.mli.com.tw/aes/winz/p-trainning-11.pdf
        if (type === CertificateInvestType.lost) {
          url = 'https://www.mli.com.tw/aes/winz/p-trainning-11.pdf';
        } else if (type === CertificateInvestType.wrong) {
          // 證書內容錯誤：連結https://www.mli.com.tw/aes/winz/p-trainning-12.pdf
          url = 'https://www.mli.com.tw/aes/winz/p-trainning-12.pdf';
        }
        break;
      default:
        break;
    }
    window.open(url, '_blank');
  }
}
