import { Component, Input, OnInit } from '@angular/core';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { ICON_PATH } from 'projects/tc-lib/src/lib/const/icon-path.const';
import { OutsideExamType } from '../../enums/quiz.enum';

@Component({
  selector: 'app-recheck-score-modal',
  templateUrl: './recheck-score-modal.component.html',
  styleUrls: ['./recheck-score-modal.component.scss']
})
export class RecheckScoreModalComponent implements OnInit {
  /** 外部測驗類型 */
  @Input() outsideExam!: OutsideExamType;
  ICON_PATH = ICON_PATH;
  OutsideExamType = OutsideExamType;
  examResultUrl = 'https://edu.tii.org.tw/exam/users/exam_result';
  constructor(
    public modal: NzModalRef
  ) { }

  ngOnInit(): void {
  }
  onExamResultUrlClick(): void {
    window.open(this.examResultUrl, '_blank');
  }
  onDownloadClick(): void {
    let url = '';
    switch (this.outsideExam) {
      case OutsideExamType.professional:
      case OutsideExamType.common:
        url = 'https://www.mli.com.tw/aes/winz/p-trainning-7.pdf';
        break;
      case OutsideExamType.foreignCurrency:
        url = 'https://www.mli.com.tw/aes/winz/p-trainning-8.pdf';
        break;
      case OutsideExamType.invest:
        url = this.examResultUrl;
        break;
      default:
        break;
    }
    window.open(url, '_blank');

  }
}
