import { Pipe, PipeTransform } from '@angular/core';
import { Nature } from '../enums/quiz.enum';

@Pipe({
  name: 'naturePipe'
})
export class NaturePipe implements PipeTransform {

  transform(type: Nature): unknown {
    switch (type) {
      case Nature.inside:
        return '內部測驗';
      case Nature.outside:
        return '外部測驗';
      default:
        return '-';
    }
  }

}
